<template>
  <div class="wrapper login-wrapper password-wrapper">
    <el-form
      v-if="!showAlignSend"
      class="password-form"
      autocomplete="off"
      :model="passwordForm"
      :rules="loginRules"
      ref="passwordForm"
      label-position="left"
    >
      <h3 class="title">找回密码</h3>
      <el-form-item prop="username">
        <span class="svg-container svg-container_login">
          <svg-icon name="email"/>
        </span>
        <el-input
          name="username"
          type="text"
          v-model="passwordForm.username"
          auto-complete="off"
          placeholder="输入邮箱地址，通过邮件找回密码"
        />
      </el-form-item>
      <el-form-item>
        <el-button
          type="primary"
          style="width:100%;"
          :loading="loading"
          @click.native.prevent="handleFunc"
        >重置密码</el-button>
      </el-form-item>
      <div class="tips">
        <router-link to="/login" style="margin-right:20px;">返回登录</router-link>
        <router-link to="/register">注册新用户</router-link>
      </div>
    </el-form>

    <div v-if="showAlignSend" class="password-form align-part">
      <h5>密码的重置链接已发送</h5>
      <p>请打开邮箱，点击里面的内容链接重置密码！</p>
      <div>
        <span>{{passwordForm.username}}没有收到？</span>
        <el-button type="text">重新发送</el-button>
      </div>
    </div>
  </div>
</template>

<script>
import {findPassword} from '@/api/user';
export default {
  name: 'find-password',
  data() {
    const validateUsername = function() {};
    return {
      passwordForm: {
        username: ''
      },
      showAlignSend: false,
      loginRules: {
        username: [
          {
            required: true,
            message: '请输入用户名',
            trigger: 'blur'
          },
          {
            min: 4,
            max: 12,
            message: '长度在 4 到 12 个字符',
            trigger: 'blur'
          }
        ]
      },
      loading: false
    };
  },
  methods: {
    handleFunc() {
      this.loading = true;
      this.$refs.passwordForm.validate(valid => {
        console.log(valid);
        if (valid) {
          findPassword(this.passwordForm.username)
            .then(result => {
              this.loading = false;
              this.$message({
                type: 'success',
                message: result.message,
                duration: 3 * 1000
              });
              this.showAlignSend = true;
            })
            .catch(err => {
              this.loading = false;
              console.log(err);
            });
        } else {
          console.log('error submit!!');
          this.loading = false;
          return false;
        }
      });
    }
  }
};
</script>

<style>
.password-wrapper .password-form {
  position: absolute;
  left: 0;
  right: 0;
  width: 520px;
  padding: 35px 35px 15px 35px;
  margin: 10vh auto 0;
  box-sizing: border-box;
}
</style>
